/* eslint-disable @typescript-eslint/no-explicit-any */
import { ENV } from "@/lib/env"

type TecdocBody = {
  provider: number | string
  locale: string            // bv. "nl"
  country: string          // bv. "nl"
  resource: string         // << naam van de functie, bv. "getVehiclesByKeyNumberPlates"
  // parameters van de functie komen HIER in dezelfde body (Pegasus JSON)
  [k: string]: any
}

export async function tecdocCall<T = any>(
  resource: string,
  params: Record<string, any> = {},
  init?: RequestInit
): Promise<T> {
  const body: TecdocBody = {
    provider: Number(ENV.TECDOC_PROVIDER_ID),
    locale: ENV.TECDOC_LANG_DEFAULT,
    country: ENV.TECDOC_LINKAGE_COUNTRY,
    resource,                // << cruciaal
    ...params,               // << functie-parameters komen top-level mee
  }

  const res = await fetch(ENV.TECDOC_BASE_JSON, {
    method: "POST",
    headers: {
      "content-type": "application/json",
      "X-Api-Key": ENV.TECDOC_API_KEY, // alternatief: ?api_key=... in de URL
    },
    body: JSON.stringify(body),
    ...init,
  })

  if (!res.ok) {
    const text = await res.text().catch(() => "")
    throw new Error(`TecDoc ${resource} ${res.status}: ${text}`)
  }
  return res.json() as Promise<T>
}

// veelgebruikte namen (ter referentie)
export const TecdocOps = {
  searchTerm: "articleSearchByTerm",
  byPlate: "getVehiclesByKeyNumberPlates",  // VRM
  byVin: "getVehiclesByVIN",
  byMotorId: "getVehicleIdsByMotor2",
  manufacturers: "getManufacturers",
  models: "getModelSeries",
  types: "getVehicleIdsByCriteria",         // of getVehicleTypes, afhankelijk van je tenant
} as const

export function qp(req: Request, key: string, dflt?: string) {
  const url = new URL(req.url); return (url.searchParams.get(key) ?? dflt)?.trim()
}
export function qpn(req: Request, key: string, dflt?: number) {
  const v = qp(req, key); if (!v) return dflt; const n = Number(v); return Number.isFinite(n) ? n : dflt
}
